.navbar {
    height: 60px;
    width: 100%;
    display: flex;
    align-items: center;
    position: relative;
    padding: 0.5rem 0;
    background-color: lightgray;
    color: black;
    box-shadow: 0 2px 2px 2px rgba(9, 9, 9, 0.23);
}

.navbar-left {
    margin-left: 3rem;
}

.brand-name{
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    text-decoration: none;
    font-weight: bold;
}

.navbar-menu {
    margin-left: auto;
}

.navbar-menu ul {
    display: flex;
    align-items: center;
    padding: 0;
}

.navbar-menu li {
    list-style-type: none;
    margin: 0 1rem;
}

.navbar-menu li a {
    text-decoration: none;
    display: block;
    width: 100%;
}

.log-in-out-button{
    cursor: pointer;
    background-color: darkslategray;
    border: 0;
    color: whitesmoke;
    padding: 12px;
}

.hamburger {
    border: 0;
    height: 40px;
    width: 40px;
    padding: 0.5rem;
    cursor: pointer;
    position: absolute;
    background: none;
    top: 50%;
    right: 25px;
    transform: translateY(-50%);
    display: none;
}

.hamburger:hover {
    background-color: darkslategray;
}

.footer{
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: darkslategray;
    color: silver;
}

@media screen and (max-width: 768px) {
    .log-in-out-button {
        width: 100%;
        height: 67px;
    }

    .hamburger {
        display: block;
    }

    .navbar-menu ul {
        display: none;
        position: absolute;
        top: 60px;
        left: 0;
        flex-direction: column;
        width: 100%;
        height: auto;
        /*height: calc(100vh - 77px);*/
        background-color: white;
        border-top: 1px solid black;
        z-index: 999;
    }

    .navbar-menu li {
        text-align: center;
        margin: 0;
    }

    .navbar-menu li a {
        color: black;
        padding: 1.5rem 0;
    }

    .navbar-menu li:hover {
        background-color: #eee;
    }

    .navbar-menu.is-active ul {
        display: block;
    }

    .navbar-left {
        margin-left: 1.5rem;
    }
}